import { PublicKey } from "@solana/web3.js";
import { z } from "zod";
import { Client } from "../platform/client";
import { buildRedeemIx } from "../instructions/redemption";
import { buildClaimCreatorFeeIx, buildCreatorRefundIx, buildCreatorRefundV2Ix, buildCreatorRefundV3Ix, buildRedeemPositionV2Ix, buildRedeemV3PositionIx } from "../instructions/idl_builders";
import type { AccountsMap } from "../idl";
import { buildAndSendV0 } from "../platform/tx";
import type { SignerLike } from "../platform/wallet";

const RedeemSchema = z.object({
  market: z.instanceof(PublicKey),
  position: z.instanceof(PublicKey),
  amount: z.bigint(),
  to: z.instanceof(PublicKey).optional(),
});

export class RedemptionModule {
  constructor(private readonly client: Client, private readonly signer: SignerLike) { }

  async redeemPosition(params: z.infer<typeof RedeemSchema>) {
    const p = RedeemSchema.parse(params);
    const ix = buildRedeemIx({
      holder: this.signer.publicKey,
      market: p.market,
      position: p.position,
      amount: p.amount,
    });

    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  // IDL-backed creator fee claim
  async claimCreatorFee(accounts: AccountsMap) {
    const ix = buildClaimCreatorFeeIx(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  // IDL-backed creator refund
  async creatorRefund(accounts: AccountsMap) {
    const ix = buildCreatorRefundIx(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  // IDL-backed creator refund V2
  async creatorRefundV2(accounts: AccountsMap) {
    const ix = buildCreatorRefundV2Ix(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  // IDL-backed creator refund V3
  async creatorRefundV3(accounts: AccountsMap) {
    const ix = buildCreatorRefundV3Ix(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  /**
   * Redeem a winning position from a resolved market
   * @param accounts - Account mapping for the redemption instruction
   * @returns Transaction signature
   */
  async redeemPositionV2(accounts: AccountsMap) {
    const ix = buildRedeemPositionV2Ix(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  /**
   * Redeem a winning position from a resolved V3 market
   * @param accounts - Account mapping for the V3 redemption instruction
   * @returns Transaction signature
   */
  async redeemV3Position(accounts: AccountsMap) {
    const ix = buildRedeemV3PositionIx(accounts, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }
}
