import { PublicKey, TransactionInstruction } from "@solana/web3.js";
import { PNP_PROGRAM_ID } from "../idl";

// Placeholder instruction builder. Replace data layout with IDL/Borsh encoding.
export function buildRedeemIx(params: {
  holder: PublicKey;
  market: PublicKey;
  position: PublicKey;
  amount: bigint;
}) {
  const { holder, market, position } = params;
  const disc = 2; // discriminator placeholder
  const data = Buffer.from([disc]);
  const keys = [
    { pubkey: holder, isSigner: true, isWritable: true },
    { pubkey: market, isSigner: false, isWritable: true },
    { pubkey: position, isSigner: false, isWritable: true },
  ];
  return new TransactionInstruction({ keys, programId: PNP_PROGRAM_ID, data });
}
