import { PublicKey } from "@solana/web3.js";
import { getAssociatedTokenAddressSync, TOKEN_PROGRAM_ID, ASSOCIATED_TOKEN_PROGRAM_ID } from "@solana/spl-token";

export function deriveMarketPda(baseMint: PublicKey, quoteMint: PublicKey, programId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("market"),
    baseMint.toBuffer(),
    quoteMint.toBuffer(),
  ], programId);
}

export function derivePositionPda(market: PublicKey, owner: PublicKey, programId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("position"),
    market.toBuffer(),
    owner.toBuffer(),
  ], programId);
}

// From IDL: global_config PDA seed: ["global_config"]
export function deriveGlobalConfigPda(programId: PublicKey) {
  return PublicKey.findProgramAddressSync([Buffer.from("global_config")], programId);
}

// From IDL: creator_fee_treasury PDA seed
// NEW format (multi-collateral): ["creator_fee_treasury", creator, collateralMint]
// LEGACY format (pre-multi-collateral): ["creator_fee_treasury", creator] - only for existing old USDC markets
export function deriveCreatorFeeTreasuryPda(
  creator: PublicKey,
  programId: PublicKey,
  collateralMint: PublicKey
) {
  // NEW format: always include collateral mint for new markets
  return PublicKey.findProgramAddressSync(
    [Buffer.from("creator_fee_treasury"), creator.toBuffer(), collateralMint.toBuffer()],
    programId
  );
}

// Legacy PDA derivation for markets created before multi-collateral update (USDC only, no collateral mint in seed)
export function deriveCreatorFeeTreasuryPdaLegacy(
  creator: PublicKey,
  programId: PublicKey
) {
  return PublicKey.findProgramAddressSync(
    [Buffer.from("creator_fee_treasury"), creator.toBuffer()],
    programId
  );
}

// Many accounts in the IDL are ATAs (buyer/creator/admin/market vault ATAs)
export function deriveAta(owner: PublicKey, mint: PublicKey, allowOwnerOffCurve = false, tokenProgramId: PublicKey = TOKEN_PROGRAM_ID, ataProgramId: PublicKey = ASSOCIATED_TOKEN_PROGRAM_ID) {
  return getAssociatedTokenAddressSync(mint, owner, allowOwnerOffCurve, tokenProgramId, ataProgramId);
}

// Market PDA per IDL derives from yes/no mints as seeds with "market"
export function deriveMarketPdaFromMints(yesMint: PublicKey, noMint: PublicKey, programId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("market"),
    yesMint.toBuffer(),
    noMint.toBuffer(),
  ], programId);
}

// Helpers to derive YES/NO token mints based on global id (u64 LE)
function u64LeBuffer(n: bigint) {
  const buf = Buffer.alloc(8);
  let x = n;
  for (let i = 0; i < 8; i++) { buf[i] = Number(x & 0xffn); x >>= 8n; }
  return buf;
}

export function deriveYesTokenMint(globalId: bigint, programId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("yes_token"),
    u64LeBuffer(globalId),
  ], programId);
}

export function deriveNoTokenMint(globalId: bigint, programId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("no_token"),
    u64LeBuffer(globalId),
  ], programId);
}

// Metaplex token metadata PDA: ["metadata", metadata_program_id, mint]
export function deriveTokenMetadataPda(mint: PublicKey, metadataProgramId: PublicKey) {
  return PublicKey.findProgramAddressSync([
    Buffer.from("metadata"),
    metadataProgramId.toBuffer(),
    mint.toBuffer(),
  ], metadataProgramId);
}
