import { Connection, Commitment, PublicKey } from "@solana/web3.js";
import { Logger, SdkConfig, TransportError } from "../types";
import { PNP_PROGRAM_ID } from "../idl";

// Network-specific program IDs
const MAINNET_PROGRAM_ID = PNP_PROGRAM_ID; // From IDL: 6fnYZUSyp3vJxTNnayq5S62d363EFaGARnqYux5bqrxb
const DEVNET_PROGRAM_ID = new PublicKey("pnpkv2qnh4bfpGvTugGDSEhvZC7DP4pVxTuDykV3BGz");

/**
 * Detects if the RPC URL is for devnet
 */
function isDevnetRpc(rpcUrl: string): boolean {
  const url = rpcUrl.toLowerCase();
  return url.includes('devnet') || url.includes('dev.') || url.includes(':8899');
}

export class Client {
  readonly connection: Connection;
  readonly programId: PublicKey;
  readonly commitment: Commitment;
  readonly logger: Logger;
  readonly isDevnet: boolean;

  constructor(cfg: SdkConfig) {
    this.commitment = cfg.commitment ?? "confirmed";
    this.connection = new Connection(cfg.rpcUrl, {
      commitment: this.commitment,
      httpHeaders: cfg.httpHeaders,
    });
    
    // Auto-detect network from RPC URL
    this.isDevnet = isDevnetRpc(cfg.rpcUrl);
    this.programId = this.isDevnet ? DEVNET_PROGRAM_ID : MAINNET_PROGRAM_ID;
    
    this.logger = cfg.logger ?? console;
    
    // Log which network we're using
    if (this.isDevnet) {
      this.logger.info?.(`[PNP SDK] Using DEVNET program: ${DEVNET_PROGRAM_ID.toBase58()}`);
    }
  }

  fail(code: string, message: string, details?: unknown) {
    this.logger.error?.(code, message, details);
    return new TransportError(`${code}: ${message}`, details);
  }
}
