import { Keypair, PublicKey, Transaction, VersionedTransaction } from "@solana/web3.js";

export interface SignerLike {
  publicKey: PublicKey;
  signTransaction(tx: Transaction | VersionedTransaction): Promise<Transaction | VersionedTransaction>;
  signAllTransactions?(txs: (Transaction | VersionedTransaction)[]): Promise<(Transaction | VersionedTransaction)[]>;
}

export class NodeKeypairSigner implements SignerLike {
  constructor(public readonly keypair: Keypair) {}
  get publicKey() { return this.keypair.publicKey; }
  async signTransaction(tx: Transaction | VersionedTransaction) {
    if (tx instanceof VersionedTransaction) {
      tx.sign([this.keypair]);
      return tx;
    }
    tx.partialSign(this.keypair);
    return tx;
  }
}
