import {
  AddressLookupTableAccount,
  ComputeBudgetProgram,
  TransactionInstruction,
  PublicKey,
  SendOptions,
  SystemProgram,
  TransactionMessage,
  VersionedTransaction,
} from "@solana/web3.js";
import { Client } from "./client";
import { SignerLike } from "./wallet";

export interface SendTxOptions extends SendOptions {
  computeUnitLimit?: number;
  computeUnitPriceMicroLamports?: number;
  addressLookupTables?: AddressLookupTableAccount[];
  simulate?: boolean;
}

export async function buildAndSendV0(
  client: Client,
  signer: SignerLike,
  ixs: readonly TransactionInstruction[],
  opts: SendTxOptions = {}
) {
  const latest = await client.connection.getLatestBlockhash({ commitment: client.commitment });

  const preIxs = [] as any[];
  if (opts.computeUnitLimit) preIxs.push(ComputeBudgetProgram.setComputeUnitLimit({ units: opts.computeUnitLimit }));
  if (opts.computeUnitPriceMicroLamports) preIxs.push(ComputeBudgetProgram.setComputeUnitPrice({ microLamports: opts.computeUnitPriceMicroLamports }));

  const msg = new TransactionMessage({
    payerKey: signer.publicKey,
    recentBlockhash: latest.blockhash,
    instructions: [...preIxs, ...ixs],
  }).compileToV0Message(opts.addressLookupTables);

  const tx = new VersionedTransaction(msg);
  await signer.signTransaction(tx);

  if (opts.simulate) {
    const sim = await client.connection.simulateTransaction(tx, { sigVerify: true });
    return { simulated: sim, signature: undefined as string | undefined };
  }

  const sig = await client.connection.sendTransaction(tx, { skipPreflight: false, maxRetries: 3, preflightCommitment: client.commitment });
  return { signature: sig };
}
