import { Connection, Keypair, PublicKey } from '@solana/web3.js';
import { Program, AnchorProvider, Wallet, Idl } from '@coral-xyz/anchor';
import { BN } from 'bn.js';
import * as anchor from '@coral-xyz/anchor';
import { PNP_IDL } from '../idl';

// Network-specific program IDs (must match platform/client.ts)
const MAINNET_PROGRAM_ID = new PublicKey("6fnYZUSyp3vJxTNnayq5S62d363EFaGARnqYux5bqrxb");
const DEVNET_PROGRAM_ID = new PublicKey("pnpkv2qnh4bfpGvTugGDSEhvZC7DP4pVxTuDykV3BGz");

/**
 * Provides a wrapper for Anchor program interactions
 * Following the same pattern as the JavaScript example
 */
export class AnchorClient {
  readonly provider: anchor.AnchorProvider;
  readonly wallet: anchor.Wallet;
  readonly program: anchor.Program;

  constructor(
    readonly connection: Connection,
    readonly signer: Keypair,
    readonly idlOverride?: Idl,
    readonly programIdOverride?: PublicKey
  ) {
    // Set up the wallet and provider exactly like in the working example
    this.wallet = new anchor.Wallet(signer);
    this.provider = new anchor.AnchorProvider(connection, this.wallet, { commitment: "confirmed" });
    
    // Set the provider globally for Anchor
    anchor.setProvider(this.provider);
    
    try {
      const verbose = process.env.VERBOSE === '1' || process.env.PNP_DEBUG === '1';
      if (verbose) {
        console.log("Initializing Anchor program with IDL");
      }
      
      // Create IDL with correct program address for the network
      const baseIdl: Idl = this.idlOverride ?? (PNP_IDL as unknown as Idl);
      const programId = this.programIdOverride;
      
      if (programId) {
        // Override the IDL address with the correct program ID for the network
        const idlWithAddress = { ...baseIdl, address: programId.toBase58() } as Idl;
        // @ts-ignore - Anchor types across versions
        this.program = new anchor.Program(idlWithAddress, this.provider);
      } else {
        // Use IDL's embedded address (mainnet default)
      // @ts-ignore - Anchor types across versions
        this.program = new anchor.Program(baseIdl, this.provider);
      }
      
      if (verbose) {
        console.log("Program loaded successfully");
        console.log(`Program ID: ${this.program.programId.toString()}`);
      }
    } catch (err) {
      console.error("Failed to initialize Anchor program:", err);
      throw err;
    }
  }

  /**
   * Convert a JavaScript BigInt or number to Anchor BN
   */
  static toBN(value: bigint | number): typeof BN.prototype {
    return new BN(value.toString());
  }
  
  /**
   * Returns the IDL for the PNP program
   */
  static async getIdl(): Promise<Idl> {
    return PNP_IDL as unknown as Idl;
  }

  static fromIdl(connection: Connection, signer: Keypair, idl: Idl) {
    return new AnchorClient(connection, signer, idl);
  }

  /**
   * Get the wallet's public key
   */
  get walletPublicKey(): PublicKey {
    return this.wallet.publicKey;
  }
}
