export type SetMarketResolvableV2Args = {
  market_id: bigint;
  resolvable: boolean;
  force_resolve?: boolean;
};

export function buildSetMarketResolvableV2Ix(
  accounts: AccountsMap,
  args: SetMarketResolvableV2Args,
  programId?: PublicKey
): TransactionInstruction {
  const market_id = new BN(args.market_id.toString());
  const resolvable = args.resolvable;
  const force_resolve = args.force_resolve !== undefined ? args.force_resolve : null;

  // Anchor expects Option<bool> for force_resolve, which can be null or a boolean
  return buildIdlIx("set_market_resolvable_v2", accounts, [market_id, resolvable, force_resolve], programId);
}

export type SetMarketResolvableV3Args = {
  resolvable: boolean;
};

export function buildSetMarketResolvableV3Ix(
  accounts: AccountsMap,
  args: SetMarketResolvableV3Args,
  programId?: PublicKey
): TransactionInstruction {
  const resolvable = args.resolvable;
  return buildIdlIx("set_market_resolvable_v3", accounts, [resolvable], programId);
}
