import { PublicKey, TransactionInstruction } from "@solana/web3.js";
import { PNP_PROGRAM_ID } from "../idl";

// Placeholder instruction builder. Replace data layout with IDL/Borsh encoding.
export function buildTradeIx(params: {
  trader: PublicKey;
  market: PublicKey;
  side: "buy" | "sell";
  price: bigint;
  size: bigint;
}) {
  const { trader, market, side } = params;
  const disc = 1; // discriminator placeholder
  const sideFlag = side === "buy" ? 0 : 1;
  const data = Buffer.from([disc, sideFlag]);
  const keys = [
    { pubkey: trader, isSigner: true, isWritable: true },
    { pubkey: market, isSigner: false, isWritable: true },
  ];
  return new TransactionInstruction({ keys, programId: PNP_PROGRAM_ID, data });
}
