  async fetchMarket(
    market: PublicKey
  ): Promise<{ publicKey: PublicKey; account: MarketType }> {
    const ai = await this.client.connection.getAccountInfo(
      market,
      this.client.commitment
    );
    if (!ai) throw new Error("market account not found");
    const acc = decodeAccount<MarketType>("Market", ai.data);
    return { publicKey: market, account: acc };
  }

  async fetchMarkets(): Promise<MarketsResponse> {
    // Derive discriminator via sha256("account:Market") and take first 8 bytes
    const { createHash } = await import("crypto");
    const discriminator = createHash("sha256")
      .update("account:Market")
      .digest()
      .subarray(0, 8);

    // Solana RPC expects memcmp.bytes as a base58 string
    const filters = [
      { memcmp: { offset: 0, bytes: bs58.encode(Buffer.from(discriminator)) } },
    ];
    const ras = await this.client.connection.getProgramAccounts(
      this.client.programId,
      {
        commitment: this.client.commitment,
        filters,
      }
    );

    const data = ras.map(({ pubkey, account }) => ({
      publicKey: pubkey.toBase58(),
      account: decodeAccount<MarketType>("Market", account.data as Buffer),
    }));
    return { count: data.length, data };
  }

  // ===== Proxy server integration =====

  /**
   * Fetch settlement criteria for a market from the proxy server
   * @param market - Market public key (as string or PublicKey)
   * @param baseUrl - Optional base URL for the proxy server
   * @returns Settlement criteria data
   */
  async fetchSettlementCriteria(
    market: string | PublicKey,
    baseUrl?: string
  ): Promise<SettlementCriteria> {
    const marketStr = typeof market === "string" ? market : market.toBase58();
    if (!marketStr || typeof marketStr !== "string") {
      throw new Error("A valid market address string is required.");
    }

    const proxyBaseUrl =
      baseUrl ||
      process.env.PROXY_BASE_URL ||
      "https://proxyserver-production-f61b.up.railway.app";
    const trimmed = proxyBaseUrl.replace(/\/+$/, "");
    const url = `${trimmed}/market-oracle/market/${encodeURIComponent(marketStr)}`;

    const res = await fetch(url, {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });

    if (!res.ok) {
      const text = await res.text().catch(() => "");
      throw new Error(
        `Proxy request failed (${res.status} ${res.statusText}): ${text}`
      );
    }

    const data = await res.json().catch(() => {
      throw new Error("Failed to parse JSON from proxy response");
    });

    // Handle different response formats
    if (
      data &&
      typeof data === "object" &&
      Object.prototype.hasOwnProperty.call(data, "data")
    ) {
      return data.data as SettlementCriteria;
    }

    return data as SettlementCriteria;
  }

  /**
   * Get settlement criteria for a market from the proxy server (alias for fetchSettlementCriteria)
   * @param market - Market public key (as string or PublicKey)
   * @param baseUrl - Optional base URL for the proxy server
   * @returns Settlement criteria data
   */
  async getSettlementCriteria(
    market: string | PublicKey,
    baseUrl?: string,
    options?: { retryDelayMs?: number; maxRetryTimeMs?: number }
  ): Promise<SettlementCriteria> {
    // Default retry configuration: retry every 2s for up to 15 minutes
    const RETRY_DELAY_MS = options?.retryDelayMs ?? 2000;
    const MAX_RETRY_TIME_MS = options?.maxRetryTimeMs ?? 15 * 60 * 1000;

    const startTime = Date.now();

    // simple sleep helper
    const sleep = (ms: number) =>
      new Promise((resolve) => setTimeout(resolve, ms));

    let attempt = 1;
    while (Date.now() - startTime < MAX_RETRY_TIME_MS) {
      try {
        const result = await this.fetchSettlementCriteria(market, baseUrl);
        if (result !== null && typeof result !== "undefined") {
          return result;
        }
        await sleep(RETRY_DELAY_MS);
        attempt++;
      } catch (_err) {
        // Swallow transient errors and retry until timeout
        await sleep(RETRY_DELAY_MS);
        attempt++;
      }
    }

    throw new Error(
      `Timed out after ${Math.floor(MAX_RETRY_TIME_MS / 1000)}s waiting for settlement criteria from proxy`
    );
  }

  /**
   * Convenience helper: wait for settlement criteria and provide a small summary.
   * - Uses getSettlementCriteria() (with retry) under the hood
   * - Derives a friendly YES/NO answer when possible
   * - Surfaces resolvable if provided by the proxy
   */
  async waitForSettlementCriteria(
    market: string | PublicKey,
    baseUrl?: string,
    options?: { retryDelayMs?: number; maxRetryTimeMs?: number }
  ): Promise<{ resolvable?: boolean; answer?: string; criteria: SettlementCriteria }> {
    const criteria = await this.getSettlementCriteria(market, baseUrl, options);

    // Derive human-friendly answer if available
    let answer: string | undefined;
    const anyCrit: any = criteria as any;
    if (typeof anyCrit?.answer === 'string' && anyCrit.answer.length > 0) {
      answer = String(anyCrit.answer).toUpperCase();
    } else if (typeof anyCrit?.winning_token_id === 'string') {
      const w = String(anyCrit.winning_token_id).toLowerCase();
      if (w === 'yes' || w === 'no') answer = w.toUpperCase();
      else answer = String(anyCrit.winning_token_id);
    }

    const resolvable: boolean | undefined =
      typeof anyCrit?.resolvable === 'boolean' ? Boolean(anyCrit.resolvable) : undefined;

    return { resolvable, answer, criteria };
  }

  /**
   * Fetch settlement data for a market from the proxy server
   * @param market - Market public key (as string or PublicKey)
   * @param baseUrl - Optional base URL for the proxy server
   * @returns Settlement data
   */
  async fetchSettlementData(
    market: string | PublicKey,
    baseUrl?: string
  ): Promise<SettlementData> {
    const marketStr = typeof market === "string" ? market : market.toBase58();
    if (!marketStr || typeof marketStr !== "string") {
      throw new Error("A valid market address string is required.");
    }

    const proxyBaseUrl =
      baseUrl ||
      process.env.PROXY_BASE_URL ||
      "https://proxyserver-production-f61b.up.railway.app";
    const trimmed = proxyBaseUrl.replace(/\/+$/, "");
    const url = `${trimmed}/market-oracle/market/${encodeURIComponent(marketStr)}/settlement-data`;

    const res = await fetch(url, {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    });

    if (!res.ok) {
      const text = await res.text().catch(() => "");
      throw new Error(
        `Proxy request failed (${res.status} ${res.statusText}): ${text}`
      );
    }

    const data = await res.json().catch(() => {
      throw new Error("Failed to parse JSON from proxy response");
    });

    // Handle different response formats
    const responseData =
      data &&
        typeof data === "object" &&
        Object.prototype.hasOwnProperty.call(data, "data")
        ? data.data
        : data;

    // Ensure the response has answer and reasoning fields
    const result: SettlementData = {
      answer: "",
      reasoning: "",
      ...responseData,
    };

    // Map fields from different response formats if needed
    if (!result.answer && responseData) {
      // Try to derive answer from winning_token_id or other fields
      if (responseData.winning_token_id === "yes") {
        result.answer = "YES";
      } else if (responseData.winning_token_id === "no") {
        result.answer = "NO";
      } else if (responseData.winning_token_id) {
        result.answer = responseData.winning_token_id.toUpperCase();
      }
    }

    // Use settlement_description as reasoning if reasoning is not provided
    if (
      !result.reasoning &&
      responseData &&
      responseData.settlement_description
    ) {
      result.reasoning = responseData.settlement_description;
    }

    return result;
  }

