import { PublicKey, TransactionInstruction } from "@solana/web3.js";
import type { Idl } from "@coral-xyz/anchor";
import { BorshCoder } from "@coral-xyz/anchor";
import idlJson from "./pnp_exchange.json" assert { type: "json" };

const DEBUG = process.env.PNP_DEBUG === '1';

// Lightweight IDL utilities to encode instructions and build account metas
export const PNP_IDL = idlJson as unknown as Idl & { address?: string };
export const PNP_PROGRAM_ID = new PublicKey(
  (PNP_IDL as any).address ?? (() => { throw new Error("IDL missing address field"); })()
);

export const idlCoder = new BorshCoder(PNP_IDL);

export type AccountsMap = Record<string, PublicKey>;

export function buildIdlIx(
  ixName: string,
  accounts: AccountsMap,
  args: any[] = [],
  programId?: PublicKey
): TransactionInstruction {
  const ixDef = PNP_IDL.instructions?.find((ix) => ix.name === ixName);
  if (!ixDef) throw new Error(`Instruction not found in IDL: ${ixName}`);

  if (DEBUG) {
    console.log(`Building instruction: ${ixName}`);
    // Pretty-print args, handling bigint, BN, PublicKey, Buffers
    console.log(
      `Instruction args:`,
      JSON.stringify(
        args.map((v) => {
          if (v && typeof v === "object") {
            // BN detection (bn.js)
            if (typeof (v as any).toString === "function" && (v as any).constructor?.name === "BN") {
              return { __bn: (v as any).toString(10) };
            }
            // PublicKey
            if (typeof (v as any).toBase58 === "function") {
              return { __pubkey: (v as any).toBase58() };
            }
            // Buffer / Uint8Array
            if (v instanceof Uint8Array) {
              return { __bytes: Buffer.from(v).toString("hex") };
            }
          }
          if (typeof v === "bigint") return v.toString();
          return v;
        }),
        null,
        2
      )
    );
    console.log(`IDL definition:`, JSON.stringify(ixDef.args, null, 2));
  }

  // Log accounts in IDL order with provided pubkeys
  try {
    if (DEBUG) {
      console.log(`Accounts for ${ixName} (IDL order):`);
      for (const acc of ixDef.accounts) {
        const name = (acc as any).name as string;
        const pk = accounts[name];
        console.log(`  ${name}: ${pk ? pk.toBase58() : '(missing)'}`);
      }
    }
  } catch (_) {
    // best-effort logging
  }

  // Ensure types match IDL specification
  if (ixDef.args && ixDef.args.length > 0) {
    if (DEBUG) {
      console.log('Checking argument types against IDL specification...');
      for (let i = 0; i < Math.min(args.length, ixDef.args.length); i++) {
        const argDef = ixDef.args[i];
        if (!argDef) continue;
        const argValue = args[i];
        console.log(`Arg ${i} (${argDef.name}): Expected type '${argDef.type}', got '${typeof argValue}' with value:`, 
          typeof argValue === 'bigint' ? argValue.toString() : argValue);
      }
    }
  }

  // Encode data using Anchor coder
  try {
    // Anchor expects a named-args object; if we received an array, map it to an object per IDL arg names
    let namedArgs: any = args;
    if (Array.isArray(args)) {
      namedArgs = {} as Record<string, any>;
      for (let i = 0; i < (ixDef.args?.length ?? 0); i++) {
        const def = ixDef.args[i];
        if (!def) continue;
        namedArgs[def.name] = args[i];
      }
      if (DEBUG) {
        console.log(`Mapped positional args to named args:`, JSON.stringify(namedArgs, (key, value) => {
          if (value && typeof value === "object") {
            if (typeof (value as any).toString === "function" && (value as any).constructor?.name === "BN") {
              return { __bn: (value as any).toString(10) };
            }
            if (typeof (value as any).toBase58 === "function") {
              return { __pubkey: (value as any).toBase58() };
            }
            if (value instanceof Uint8Array) {
              return { __bytes: Buffer.from(value).toString("hex") };
            }
          }
          if (typeof value === "bigint") return value.toString();
          return value;
        }, 2));
      }
    }

    const data = idlCoder.instruction.encode(ixName as any, namedArgs);
    if (DEBUG) {
      console.log(`Instruction data encoded successfully, length: ${data.length} bytes`);
      console.log(`First 20 bytes: ${Buffer.from(data).toString('hex').substring(0, 40)}...`);
    }

    // For critical debugging: decode the first fields directly from the encoded buffer
    try {
      if (DEBUG) {
        const buf = Buffer.from(data);
        // Discriminator is first 8 bytes
        const off = 8;
        const readU64LE = (b: Buffer, o: number) =>
          BigInt(b.readUInt32LE(o)) + (BigInt(b.readUInt32LE(o + 4)) << 32n);

        if (ixName === "mint_decision_tokens") {
          const amountLE = readU64LE(buf, off);
          const buyFlag = buf[off + 8] === 1;
          const minimumOutLE = readU64LE(buf, off + 9);
          console.log(
            `[buildIdlIx:${ixName}] Encoded fields -> amount(u64): ${amountLE.toString()}, buy_yes_token(bool): ${buyFlag}, minimum_out(u64): ${minimumOutLE.toString()}`
          );
        }
        if (ixName === "burn_decision_tokens") {
          const amountLE = readU64LE(buf, off);
          const burnFlag = buf[off + 8] === 1;
          console.log(
            `[buildIdlIx:${ixName}] Encoded fields -> amount(u64): ${amountLE.toString()}, burn_yes_token(bool): ${burnFlag}`
          );
        }
      }
    } catch (e) {
      if (DEBUG) console.warn(`[buildIdlIx:${ixName}] Post-encode field inspection failed:`, (e as any)?.message ?? String(e));
    }

    const programIdToUse = programId || PNP_PROGRAM_ID;
    return new TransactionInstruction({ programId: programIdToUse, keys: constructAccountMetas(ixDef, accounts), data });
  } catch (err: any) {
    if (DEBUG) console.error(`Failed to encode instruction data: ${err.message || err}`);
    throw err;
  }
}

function constructAccountMetas(ixDef: any, accounts: AccountsMap) {
  // Map accounts in IDL order
  const metas: { pubkey: PublicKey; isSigner: boolean; isWritable: boolean }[] = [];
  for (const acc of ixDef.accounts) {
    const a: any = acc as any;
    const name: string = a.name as string;
    const optional: boolean = Boolean(a.optional);
    const pubkey = accounts[name];
    if (!pubkey) {
      if (optional) {
        // Skip optional accounts if not supplied
        continue;
      }
      throw new Error(`Missing account for ${ixDef.name}: ${name}`);
    }
    const isSigner: boolean = Boolean(a.signer);
    const isWritable: boolean = Boolean(a.writable);
    metas.push({ pubkey, isSigner, isWritable });
  }
  return metas;
}

  // Removed duplicated code (replaced by constructAccountMetas function)

export function decodeAccount<T = any>(accountName: string, data: Buffer): T {
  return idlCoder.accounts.decode(accountName as any, data) as T;
}
