import { PublicKey, SystemProgram, ComputeBudgetProgram } from "@solana/web3.js";
import {
  TOKEN_PROGRAM_ID,
  ASSOCIATED_TOKEN_PROGRAM_ID,
  getAssociatedTokenAddress,
} from "@solana/spl-token";
import { BN } from "bn.js";
import * as anchor from "@coral-xyz/anchor";
import { AnchorClient } from "../lib/anchor-client";

/**
 * Market module that uses direct Anchor program calls
 */
export class AnchorMarketModule {
  constructor(private anchorClient: AnchorClient) { }

  /**
   * Create a new prediction market using direct Anchor program calls
   * Following the same structure as the working JavaScript example
   */
  async createMarket(params: {
    question: string;
    initialLiquidity: bigint;
    endTime: bigint;
    collateralTokenMint: PublicKey;
    creator?: PublicKey; // Optional: explicit creator (can be different from payer)
    yesOddsBps?: number; // Optional: Custom YES odds (100-9900 bps). Default 5000 (50/50)
    oracle?: PublicKey; // Optional: Custom oracle for settlement. Default uses global oracle
  }) {
    const { question, initialLiquidity, endTime, collateralTokenMint } = params;
    const creatorOverride: PublicKey = params.creator ?? this.anchorClient.walletPublicKey; // default to payer

    // Convert bigint to BN just like in the JavaScript example
    const initialLiquidityBN = AnchorClient.toBN(initialLiquidity);
    const endTimeBN = AnchorClient.toBN(endTime);

    console.log(" Creating market with parameters:");
    console.log("   Question:", question);
    console.log("   Initial Liquidity:", initialLiquidityBN.toString());
    console.log("   End Time:", new Date(Number(endTime) * 1000).toISOString());
    console.log("   Collateral Token Mint:", collateralTokenMint.toString());
    console.log("   Creator (for fees/ownership):", creatorOverride.toBase58());

    // 1. Get global config and ID
    const [globalConfig] = PublicKey.findProgramAddressSync(
      [Buffer.from("global_config")],
      this.anchorClient.program.programId
    );
    console.log("Global Config:", globalConfig.toBase58());

    // Fetch global config to get the current global_id (BN)
    const globalConfigAccount = await (this.anchorClient.program.account as any).globalConfig.fetch(globalConfig);
    const currentGlobalId = globalConfigAccount.globalId;
    console.log("Current Global ID:", currentGlobalId.toString());

    // Derive market extension PDA if custom odds or oracle is provided
    let marketExtension: PublicKey | null = null;
    if (params.yesOddsBps !== undefined || params.oracle !== undefined) {
      [marketExtension] = PublicKey.findProgramAddressSync(
        [Buffer.from("market_ext"), currentGlobalId.toArrayLike(Buffer, "le", 8)],
        this.anchorClient.program.programId
      );
      console.log("Market Extension PDA:", marketExtension.toBase58());
      if (params.yesOddsBps !== undefined) {
        console.log("Custom YES Odds (BPS):", params.yesOddsBps);
      }
      if (params.oracle !== undefined) {
        console.log("Custom Oracle:", params.oracle.toBase58());
      }
    }

    // 2. Derive V2 market PDA: ["market-v2", global_id (le u64)]
    const [market] = PublicKey.findProgramAddressSync(
      [Buffer.from("market-v2"), currentGlobalId.toArrayLike(Buffer, "le", 8)],
      this.anchorClient.program.programId
    );
    console.log("Market PDA:", market.toBase58());

    // 3. ATAs required for V2
    // IDL now expects payer_collateral_token_account (owned by payer)
    const payerCollateralTokenAccount = await getAssociatedTokenAddress(
      collateralTokenMint,
      this.anchorClient.walletPublicKey
    );

    const marketReserveVault = await getAssociatedTokenAddress(
      collateralTokenMint,
      market,
      true // allowOwnerOffCurve
    );

    // 4. Derive creator fee treasury
    // NEW format (multi-collateral): ["creator_fee_treasury", creator, collateralMint]
    const [creatorFeeTreasury] = PublicKey.findProgramAddressSync(
      [Buffer.from("creator_fee_treasury"), creatorOverride.toBuffer(), collateralTokenMint.toBuffer()],
      this.anchorClient.program.programId
    );

    try {
      // Add compute budget instruction to handle complex transaction
      const computeBudgetIx = ComputeBudgetProgram.setComputeUnitLimit({
        units: 400_000,
      });

      // 5. Direct program method call for V2
      const txSignature = await (this.anchorClient.program.methods as any)
        .createMarketV2(
          question,
          initialLiquidityBN,
          endTimeBN,
          creatorOverride,
          params.yesOddsBps ?? null,  // Optional: Custom YES odds (100-9900 bps)
          params.oracle ?? null        // Optional: Custom oracle
        )
        .accounts({
          payer: this.anchorClient.walletPublicKey,
          creator: creatorOverride,
          market: market,
          marketExtension,  // Optional account for custom odds/oracle
          globalConfig: globalConfig,
          collateralTokenMint: collateralTokenMint,
          payerCollateralTokenAccount: payerCollateralTokenAccount,
          marketReserveVault: marketReserveVault,
          creatorFeeTreasury: creatorFeeTreasury,
          tokenProgram: TOKEN_PROGRAM_ID,
          associatedTokenProgram: ASSOCIATED_TOKEN_PROGRAM_ID,
          systemProgram: SystemProgram.programId,
        })
        .preInstructions([computeBudgetIx])
        .signers([this.anchorClient.signer])
        .rpc();

      console.log(" Market created successfully!");
      console.log("Transaction signature:", txSignature);

      // 6. Fetch and display the created market (optional)
      try {
        const marketAccount = await (this.anchorClient.program.account as any).market.fetch(market);
        console.log("\n Market Details:");
        console.log("------------------------");
        console.log("Market ID:", marketAccount.id.toString());
        console.log("Question:", marketAccount.question);
        console.log("Creator:", marketAccount.creator.toBase58());
        console.log("Initial Liquidity:", marketAccount.initialLiquidity.toString());
        console.log("Market Reserves:", marketAccount.marketReserves.toString());
        console.log("End Time:", new Date(marketAccount.endTime.toNumber() * 1000).toISOString());
        console.log("Resolved:", marketAccount.resolved);
        console.log("------------------------");

        return {
          signature: txSignature,
          market,
          marketDetails: marketAccount
        };
      } catch (err) {
        // Return basic info even if we can't fetch market details
        return {
          signature: txSignature,
          market,
        };
      }
    } catch (error: any) {
      console.error(" Error creating market:", error);

      if (error.logs) {
        console.error("Program logs:");
        error.logs.forEach((log: string) => console.error("  ", log));
      }

      throw error;
    }
  }
}
