// Keep SDK types independent of web3.js imports to avoid resolution issues in pure type files
export type Commitment = "processed" | "confirmed" | "finalized";

export interface Logger {
  debug?: (...args: any[]) => void;
  info?: (...args: any[]) => void;
  warn?: (...args: any[]) => void;
  error?: (...args: any[]) => void;
}

export interface SdkConfig {
  rpcUrl: string;
  commitment?: Commitment;
  httpHeaders?: Record<string, string>;
  priorityFeeMicroLamports?: number;
  computeUnitLimit?: number;
  computeUnitPriceMicroLamports?: number;
  addressLookupTables?: unknown[];
  logger?: Logger;
}

export class SdkError extends Error {
  code: string;
  details?: unknown;
  constructor(code: string, message: string, details?: unknown) {
    super(message);
    this.name = "SdkError";
    this.code = code;
    this.details = details;
  }
}

export class ValidationError extends SdkError {
  constructor(message: string, details?: unknown) {
    super("VALIDATION_ERROR", message, details);
    this.name = "ValidationError";
  }
}

export class TransportError extends SdkError {
  constructor(message: string, details?: unknown) {
    super("TRANSPORT_ERROR", message, details);
    this.name = "TransportError";
  }
}

export class ProgramError extends SdkError {
  programErrorCode?: number;
  logs?: string[];
  constructor(
    message: string,
    details?: unknown,
    programErrorCode?: number,
    logs?: string[]
  ) {
    super("PROGRAM_ERROR", message, details);
    this.name = "ProgramError";
    this.programErrorCode = programErrorCode;
    this.logs = logs;
  }
}

// ===== PNP raw IDL types =====

// Web3-agnostic pubkey representation for raw account typing
export type PubkeyLike = string;

// Numeric fields from IDL may arrive as bigint | number | string depending on decoder
export type U64Like = bigint | number | string;

// Enum may arrive as tagged object, string, or number depending on decoder
export type WinningTokenLike =
  | "none"
  | "yes"
  | "no"
  | number
  | Record<string, unknown>
  | null;

// Raw Market account shape (snake_case per IDL)
export interface MarketType {
  id: U64Like;
  resolved: boolean;
  market_reserves: U64Like;
  collateral_token: PubkeyLike;
  winning_token_id: WinningTokenLike;
  yes_token_mint: PubkeyLike;
  no_token_mint: PubkeyLike;
  yes_token_supply_minted: U64Like;
  no_token_supply_minted: U64Like;
  creator: PubkeyLike;
  creator_fee_treasury: PubkeyLike;
  question: string;
  initial_liquidity: U64Like;
  end_time: U64Like;
  bump: number;
  creation_time: U64Like;
  resolvable: boolean;
  force_resolve: boolean;
  version: number;
}

export interface MarketsResponse {
  count: number;
  data: Array<{ publicKey: string; account: MarketType }>;
}

// Global configuration account shape (snake_case per IDL)
export interface GlobalConfigType {
  admin: PubkeyLike;
  oracle_program: PubkeyLike;
  fee: U64Like;
  creator_fee: U64Like;
  bump: number;
  yes_global_metadata: string;
  no_global_metadata: string;
  collateral_token_mint: PubkeyLike;
  global_id: U64Like;
  min_liquidity: U64Like;
  buffer_period: U64Like;
  burn_fee: U64Like;
  trading_paused: boolean;
}

// Settlement criteria response from proxy server
export interface SettlementCriteria {
  category: string;
  reasoning: string;
  resolvable: boolean;
  resolution_sources: string[];
  settlement_criteria: string;
  suggested_improvements: string;
}

// Settlement data response from proxy server
export interface SettlementData {
  answer: string; // The resolution answer (e.g., "YES", "NO", "INVALID")
  reasoning: string; // Explanation for the resolution
  resolved?: boolean;
  winning_token_id?: string;
  resolution_time?: string;
  settlement_description?: string;
  resolution_source?: string;
}

// Market metadata response from proxy server batch-data endpoint
export interface MarketMeta {
  market: string;
  market_volume: number;
  image: string | null;
  initial_liquidity: number;
}

// ===== Operation result types =====
/**
 * Web3-agnostic shape for the result of a P2P market operation.
 * Note: The Anchor-based module returns actual PublicKey instances at runtime.
 * When exposing results at the SDK boundary, prefer serializing these to base58 strings to match P2PMarketResponse.
 */
export interface P2PMarketResponse {
  signature: string;
  market: PubkeyLike;
  yesTokenMint: PubkeyLike;
  noTokenMint: PubkeyLike;
}
