import { PublicKey, SystemProgram } from "@solana/web3.js";
import { z } from "zod";
import { Client } from "../platform/client";
import { buildCreateMarketIxIdl, type CreateMarketArgs, buildSetMarketResolvableV2Ix, type SetMarketResolvableV2Args, buildSetMarketResolvableV3Ix, type SetMarketResolvableV3Args } from "../instructions/idl_builders";
import { decodeAccount } from "../idl";
import type { AccountsMap } from "../idl";
import { buildAndSendV0 } from "../platform/tx";
import type { SignerLike } from "../platform/wallet";
import {
  deriveGlobalConfigPda,
  deriveAta,
  deriveCreatorFeeTreasuryPda,
  deriveYesTokenMint,
  deriveNoTokenMint,
  deriveTokenMetadataPda,
} from "../instructions/pdas";
import { TOKEN_PROGRAM_ID, TOKEN_2022_PROGRAM_ID, ASSOCIATED_TOKEN_PROGRAM_ID } from "@solana/spl-token";

const DEBUG = process.env.PNP_DEBUG === '1';

// Default collateral mint used when none is provided by caller
// Using USDC on Mainnet: EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v
const DEFAULT_COLLATERAL_MINT = new PublicKey('EPjFWdd5AufqSSqeM2qN1xzybapC8G4wEGGkZwyTDt1v');

const CreateMarketSchema = z.object({
  baseMint: z.instanceof(PublicKey).optional(),
  quoteMint: z.instanceof(PublicKey).optional(),
  question: z.string().optional(),
  initialLiquidity: z.bigint().optional(),
  endTime: z.bigint().optional(),
  tickSize: z.bigint().optional(),
  lotSize: z.bigint().optional(),
});

export class MarketModule {
  constructor(private readonly client: Client, private readonly signer: SignerLike) { }

  async createMarket(params: z.infer<typeof CreateMarketSchema>) {
    const p = CreateMarketSchema.parse(params);
    if (!p.question || !p.initialLiquidity || !p.endTime) {
      throw new Error("createMarket now requires question, initialLiquidity, and endTime. The legacy v1 path has been removed.");
    }
    const collateral = p.baseMint ?? DEFAULT_COLLATERAL_MINT;
    return this.createMarketDerived({
      question: p.question,
      initialLiquidity: p.initialLiquidity,
      endTime: p.endTime,
      collateralMint: collateral,
    });
  }

  // IDL-backed create market allowing explicit accounts from the pnp.exchange IDL
  async createMarketIdl(accounts: AccountsMap, args: CreateMarketArgs) {
    const ix = buildCreateMarketIxIdl(accounts, args, this.client.programId);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    return { signature: res.signature };
  }

  // High-level helper: derive all accounts and create market (V2)
  async createMarketDerived(params: {
    question: string;
    initialLiquidity: bigint;
    endTime: bigint;
    collateralMint?: PublicKey;
  }) {
    const { question, initialLiquidity, endTime } = params;
    const collateralMint = params.collateralMint ?? DEFAULT_COLLATERAL_MINT;

    // 1) global_config PDA
    const [globalConfig] = deriveGlobalConfigPda(this.client.programId);
    const ai = await this.client.connection.getAccountInfo(globalConfig, this.client.commitment);
    if (!ai) throw new Error("global_config not initialized on-chain");

    // 2) decode to read global_id (u64) and convert to bigint
    const globalCfg = decodeAccount<any>("GlobalConfig", ai.data);
    if (DEBUG) {
      try {
        console.log('GlobalConfig account data:', JSON.stringify(globalCfg, null, 2));
      } catch { }
    }

    // Extract globalId from the correct field in the structure
    // The exact field name might be 'globalId', 'global_id', or another property
    let globalId: bigint;
    if (globalCfg.globalId) {
      globalId = BigInt(globalCfg.globalId.toString());
    } else if (globalCfg.global_id) {
      globalId = BigInt(globalCfg.global_id.toString());
    } else {
      // Find the field containing the global ID by inspecting all properties
      const keys = Object.keys(globalCfg);
      if (DEBUG) console.log('Available keys in GlobalConfig:', keys);

      // Try to find a property that looks like an ID (either a BN or a number)
      const idField = keys.find(k =>
        typeof (globalCfg as any)[k]?.toNumber === 'function' ||
        typeof (globalCfg as any)[k]?.toString === 'function' ||
        typeof (globalCfg as any)[k] === 'number'
      );

      if (idField) {
        if (DEBUG) console.log(`Using ${idField} as the global ID field`);
        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
        globalId = BigInt((globalCfg as any)[idField].toString());
      } else {
        throw new Error(`Could not find global ID in GlobalConfig account. Available fields: ${keys.join(', ')}`);
      }
    }

    // 3) Derive V2 market PDA: ["market-v2", global_id (le u64)]
    const gidLe = Buffer.alloc(8);
    gidLe.writeBigUInt64LE(globalId);
    const [market] = PublicKey.findProgramAddressSync(
      [Buffer.from("market-v2"), gidLe],
      this.client.programId
    );

    // Derive market extension PDA
    // Uses "market_ext" seed + global_id buffer (must match on-chain program)
    const [marketExtension] = PublicKey.findProgramAddressSync(
      [Buffer.from("market_ext"), gidLe],
      this.client.programId
    );

    // 4) ATAs (creator and market reserve)
    const payer = this.signer.publicKey;
    const creator = payer; // Set creator same as payer
    const payerCollateralTokenAccount = deriveAta(payer, collateralMint);
    const marketReserveVault = deriveAta(market, collateralMint, true);

    // 5) Derive creator fee treasury (with collateral mint for multi-collateral support)
    const [creatorFeeTreasury] = deriveCreatorFeeTreasuryPda(creator, this.client.programId, collateralMint);

    // 6) assemble accounts per IDL (V2) - NOTE: snake_case to match IDL
    const accounts: Record<string, PublicKey> = {
      payer,
      creator,
      market,
      market_extension: marketExtension, // Always include (program may expect it)
      global_config: globalConfig,
      collateral_token_mint: collateralMint,
      payer_collateral_token_account: payerCollateralTokenAccount,
      market_reserve_vault: marketReserveVault,
      creator_fee_treasury: creatorFeeTreasury,
      token_program: TOKEN_PROGRAM_ID,
      associated_token_program: ASSOCIATED_TOKEN_PROGRAM_ID,
      system_program: SystemProgram.programId,
    };

    // 7) build ix and send (IDL builder selects create_market_v2)
    // Pass programId to ensure correct network program is used
    // IMPORTANT: Pass yes_odds_bps to trigger market_extension initialization
    // Without this, the extension account is NOT created, causing setMarketResolvableV2 to fail
    const ix = buildCreateMarketIxIdl(accounts, {
      question,
      initial_liquidity: initialLiquidity,
      end_time: endTime,
      creator, // Pass the creator explicitly
      yes_odds_bps: 5000, // Default 50/50 odds - REQUIRED to initialize market_extension
    }, this.client.programId);

    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 400_000,
    });

    return {
      signature: res.signature,
      market,
    };
  }

  // ===== Fetchers =====
  async fetchGlobalConfig() {
    const [globalConfig] = deriveGlobalConfigPda(this.client.programId);
    const ai = await this.client.connection.getAccountInfo(globalConfig, this.client.commitment);
    if (!ai) throw new Error("global_config not found");
    const acc = decodeAccount<any>("GlobalConfig", ai.data);
    return { publicKey: globalConfig, account: acc };
  }

  async fetchMarket(market: PublicKey) {
    const ai = await this.client.connection.getAccountInfo(market, this.client.commitment);
    if (!ai) throw new Error("market account not found");
    const acc = decodeAccount<any>("Market", ai.data);
    return { publicKey: market, account: acc };
  }

  // ===== Set Market Resolvable (V2) =====
  /**
   *  DEVNET/TESTING ONLY - This function is for devnet and testing purposes only.
   * It will NOT work on mainnet. Use this only for development and testing.
   */
  async setMarketResolvableIdl(accounts: AccountsMap, args: SetMarketResolvableV2Args) {
    const ix = buildSetMarketResolvableV2Ix(accounts, args);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    if (!res.signature) {
      throw new Error("Transaction failed: No signature returned");
    }
    return { signature: res.signature };
  }

  /**
   * Set market resolvable status for V2 markets.
   * 
   *  DEVNET/TESTING ONLY - This function is for devnet and testing purposes only.
   * It will NOT work on mainnet. Use this only for development and testing.
   * 
   * @param params - Parameters for setting market resolvable status
   * @param params.market - Market public key
   * @param params.resolvable - New resolvable status (true/false)
   * @param params.forceResolve - Optional: force resolve (allow immediate refunds)
   * @returns Transaction signature
   */
  async setMarketResolvable(params: {
    market: PublicKey;
    resolvable: boolean;
    forceResolve?: boolean;
  }) {
    const { market, resolvable, forceResolve } = params;

    // 1. Fetch market to get market_id
    const { account: marketAccount } = await this.fetchMarket(market);

    // Extract market_id - handle different possible field names and types
    let marketId: bigint;
    if (marketAccount.id !== undefined && marketAccount.id !== null) {
      marketId = BigInt(marketAccount.id.toString());
    } else if ((marketAccount as any).market_id !== undefined) {
      marketId = BigInt((marketAccount as any).market_id.toString());
    } else {
      throw new Error("Could not extract market_id from market account. Market account missing 'id' field.");
    }

    if (marketId === 0n) {
      throw new Error("Market ID is zero, which is invalid");
    }

    // 2. Fetch global config PDA (needed for instruction accounts)
    const [globalConfig] = deriveGlobalConfigPda(this.client.programId);
    const globalConfigInfo = await this.client.connection.getAccountInfo(globalConfig, this.client.commitment);
    if (!globalConfigInfo) throw new Error("global_config not initialized on-chain");

    // NOTE: Admin check is disabled - on-chain program will enforce admin requirement

    // 3. Get collateral token mint from market
    const collateralTokenMint = new PublicKey(marketAccount.collateral_token || marketAccount.collateralToken);
    const creator = new PublicKey(marketAccount.creator);

    // 4. Derive YES and NO token mints from market_id
    const [yesTokenMint] = deriveYesTokenMint(marketId, this.client.programId);
    const [noTokenMint] = deriveNoTokenMint(marketId, this.client.programId);

    // 5. Derive creator token accounts (ATAs)
    const creatorYesTokenAccount = deriveAta(creator, yesTokenMint);
    const creatorNoTokenAccount = deriveAta(creator, noTokenMint);

    // 6. Derive token metadata PDAs
    // Standard Metaplex Token Metadata Program ID: metaqbxxUerdq28cj1RbAWkYQm3ybzjb6a8bt518x1s
    const TOKEN_METADATA_PROGRAM_ID = new PublicKey("metaqbxxUerdq28cj1RbAWkYQm3ybzjb6a8bt518x1s");
    const [yesMetadata] = deriveTokenMetadataPda(yesTokenMint, TOKEN_METADATA_PROGRAM_ID);
    const [noMetadata] = deriveTokenMetadataPda(noTokenMint, TOKEN_METADATA_PROGRAM_ID);

    // 7. Determine token program (check if using TOKEN_2022 or standard TOKEN_PROGRAM)
    const collateralMintInfo = await this.client.connection.getAccountInfo(collateralTokenMint, this.client.commitment);
    if (!collateralMintInfo) throw new Error("collateral_token_mint not found");
    const tokenProgram = collateralMintInfo.owner.equals(TOKEN_2022_PROGRAM_ID) ? TOKEN_2022_PROGRAM_ID : TOKEN_PROGRAM_ID;

    // 8. Derive market extension PDA
    // Uses "market_ext" seed + market_id buffer (must match on-chain program)
    const marketIdBuffer = Buffer.alloc(8);
    marketIdBuffer.writeBigUInt64LE(marketId);
    const [marketExtension] = PublicKey.findProgramAddressSync(
      [Buffer.from("market_ext"), marketIdBuffer],
      this.client.programId
    );

    // 9. Assemble accounts per IDL (snake_case to match IDL)
    const accounts: AccountsMap & Record<string, PublicKey> = {
      authority: this.signer.publicKey,
      global_config: globalConfig,
      market,
      market_extension: marketExtension, // Always include (optional in IDL but position depends on it)
      collateral_token_mint: collateralTokenMint,
      yes_token_mint: yesTokenMint,
      no_token_mint: noTokenMint,
      creator,
      creator_yes_token_account: creatorYesTokenAccount,
      creator_no_token_account: creatorNoTokenAccount,
      token_metadata_program: TOKEN_METADATA_PROGRAM_ID,
      yes_metadata: yesMetadata,
      no_metadata: noMetadata,
      token_program: tokenProgram,
      associated_token_program: ASSOCIATED_TOKEN_PROGRAM_ID,
      system_program: SystemProgram.programId,
      rent: new PublicKey("SysvarRent111111111111111111111111111111111"),
    };

    // 9. Build instruction and send
    const ix = buildSetMarketResolvableV2Ix(accounts, {
      market_id: marketId,
      resolvable,
      force_resolve: forceResolve,
    }, this.client.programId);

    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });

    if (!res.signature) {
      throw new Error("Transaction failed: No signature returned");
    }

    return { signature: res.signature };
  }

  // ===== Set Market Resolvable (V3/P2P) =====
  /**
   *  DEVNET/TESTING ONLY - This function is for devnet and testing purposes only.
   * It will NOT work on mainnet. Use this only for development and testing.
   */
  async setMarketResolvableP2pIdl(accounts: AccountsMap, args: SetMarketResolvableV3Args) {
    const ix = buildSetMarketResolvableV3Ix(accounts, args);
    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });
    if (!res.signature) {
      throw new Error("Transaction failed: No signature returned");
    }
    return { signature: res.signature };
  }

  /**
   * Set market resolvable status for V3/P2P markets.
   * 
   *  DEVNET/TESTING ONLY - This function is for devnet and testing purposes only.
   * It will NOT work on mainnet. Use this only for development and testing.
   * 
   * @param params - Parameters for setting market resolvable status
   * @param params.market - Market public key
   * @param params.resolvable - New resolvable status (true/false)
   * @returns Transaction signature
   */
  async setMarketResolvableP2p(params: {
    market: PublicKey;
    resolvable: boolean;
  }) {
    const { market, resolvable } = params;

    // 1. Fetch global config PDA (needed for instruction accounts)
    const [globalConfig] = deriveGlobalConfigPda(this.client.programId);
    const globalConfigInfo = await this.client.connection.getAccountInfo(globalConfig, this.client.commitment);
    if (!globalConfigInfo) throw new Error("global_config not initialized on-chain");

    // NOTE: Admin check is disabled - on-chain program will enforce admin requirement

    // 2. Assemble accounts per IDL (snake_case to match IDL)
    const accounts: AccountsMap & Record<string, PublicKey> = {
      authority: this.signer.publicKey,
      global_config: globalConfig,
      market,
    };

    // 3. Build instruction and send
    const ix = buildSetMarketResolvableV3Ix(accounts, {
      resolvable,
    }, this.client.programId);

    const res = await buildAndSendV0(this.client, this.signer, [ix], {
      computeUnitLimit: 800000,
    });

    if (!res.signature) {
      throw new Error("Transaction failed: No signature returned");
    }

    return { signature: res.signature };
  }
}
